<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :recordCount="recordCount"
            :disCodeOption="disCodeOption" @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete"
            @onView="handleView" @onEdit="handleEdit" @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">
                    <el-col :span="5">
                        <span class="techo-list__header-col-txt-4">案卷名称：</span>
                        <div class="techo-list__header-col-left-4">
                            <el-input placeholder="请输入案卷名称" :maxlength="10" size="small" class="techo-list__header-select-full"
                                clearable v-model="queryForm.name"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="5">
                        <span class="techo-list__header-col-txt-3">学校：</span>
                        <div class="techo-list__header-col-left-3">
                            <el-select placeholder="请选择学校" size="small" filterable change-on-select clearable v-model="queryForm.schoolId">
                                <el-option v-for="(item, index) in schListOption" :label="item.name" :value="item.id"
                                    :key="index"></el-option>
                            </el-select>
                        </div>
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>

        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey"></techo-details>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/campus_security_service.js";
import vAddDialog from "./preferst-case-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "涉及师生安全案（事）件",
      schListOption: [],
      queryForm: {
        districtCode: "",
        schoolId: "",
        name: ""
      },
      header: {
        案卷编号: "caseNumber",
        案卷名称: "caseName",
        学校: "dicMap.schoolName",
        发生日期: "happenDate",
        //发案地: "casePlace",
        //发生地: "happenPlace",
        案事件性质: "dicMap.caseNature",
        //案事件情况: "caseDesc",
        //主犯嫌疑人证件类型: "dicMap.suspectsIdcode",
        //主犯嫌疑人证件号码: "suspectsCardNo",
        //主犯嫌疑人姓名: "suspectsName",
        是否破案: "dicMap.isCaseSolve"
        //作案人数: "caseuserCount",
        //在逃人数: "escapedCount",
        //抓捕人数: "arrestCount",
        //案件侦破情况:"detectedDesc",
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "preferstCase"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
    service.requestCampusSecurityByCode(this.queryForm.districtCode, data => {
      this.schListOption = data.list;
    });
    // service.requestPageCampusSecurity(
    //   {
    //     name: "",
    //     districtCode: this.queryForm.districtCode,
    //     page: 1,
    //     pageSize: 1000
    //   },
    //   data => {
    //     this.schListOption = data.list;
    //     if (data.list.length != 0) {
    //       this.queryForm.schId = data[0].id;
    //     }
    //   }
    // );
  },
  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    },
    // "queryForm.districtCode"(districtCode) {
    //   service.requestPageCampusSecurity(
    //     { name: "", districtCode: districtCode, page: 1, pageSize: 1000 },
    //     data => {
    //       this.schListOption = data.list;
    //       if (data.list.length != 0) {
    //         this.queryForm.schId = data[0].id;
    //       }
    //     }
    //   );
    // }
    "queryForm.districtCode": {
      handler(code) {
        service.requestCampusSecurityByCode(code, data => {
          this.schListOption = data.list;
        });
      }
    }
  },
  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加  */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs[
        "vAddDialog"
      ].dialogForm.districtCode = this.queryForm.districtCode;
      this.$refs["vAddDialog"].schListOption = this.schListOption;
    },
    /** 删除   */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeletePreferstCase(item.id, data => {
            count == tableSelection.length - 1 && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "信息删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "信息删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].disabled = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: "",
        schoolId: ""
      };
    },

    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPagePreferstCase(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPagePreferstCase(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              if (item.dicMap) {
                item.dicMap.schoolName = item.schoolName;
                item.dicMap.isCaseSolve = item.isCaseSolve == 1 ? "是" : "否";
              }
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            if (item.dicMap) {
              item.dicMap.schoolName = item.schoolName;
              item.dicMap.isCaseSolve = item.isCaseSolve == 1 ? "是" : "否";
            }
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>