<template>
  <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen>
    <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="150px">
      <el-row :gutter="15">
        <el-col :span="12">                 
          <el-form-item label="学校" prop="schoolId" >
              <el-select placeholder="请选择学校" clearable v-model="dialogForm.schoolId" @change="handleChangeCode">
              <el-option v-for="(item, index) in schListOption" :label="item.name" :value="item.id" :key="index"></el-option>
              </el-select>
          </el-form-item>
          <el-form-item label="发案地" prop="casePlace">
            <el-select placeholder="请选择发案地" clearable v-model="dialogForm.casePlace">
                <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                
            </el-select>           
          </el-form-item> 
          <el-form-item label="案（事）件编号" prop="caseNumber">
            <el-input v-model="dialogForm.caseNumber" placeholder="请填写案（事）件编号"></el-input>
          </el-form-item> 
          <el-form-item label="案（事）件性质" prop="caseNature" label-width="150px">
          <el-select placeholder="请选择案（事）件性质" clearable v-model="dialogForm.caseNature">
                <el-option v-for="(item, index) in dic['案事件性质']" :label="item.name" :value="item.code" :key="index"></el-option>
          </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">                            
          <el-form-item label="发生日期" prop="happenDate">
            <el-date-picker v-model="dialogForm.happenDate" type="date" placeholder="请选择发生日期"></el-date-picker>
          </el-form-item>  
          <el-form-item label="发生地" prop="happenPlace" >
            <el-input v-model="dialogForm.happenPlace"  placeholder="请填写发生地"></el-input>
          </el-form-item>   
          <el-form-item label="案（事）件名称" prop="caseName">
            <el-input v-model="dialogForm.caseName" placeholder="请填写案（事）件名称"></el-input>
          </el-form-item>    
          <el-form-item label="主犯（嫌疑人）姓名" prop="suspectsName" label-width="150px">
            <el-input v-model="dialogForm.suspectsName"  placeholder="主犯（嫌疑人）姓名"></el-input>
          </el-form-item>   
        </el-col>
      </el-row>
      <el-row :gutter="15">        
        <el-col :span="12">
         <el-form-item label="主犯（嫌疑人）证件类型" prop="suspectsIdcode" label-width="180px">
            <el-select placeholder="请选择证件类型" clearable v-model="dialogForm.suspectsIdcode">
                <el-option v-for="(item, index) in dic['证件代码']" :label="item.name" :value="item.code" :key="index"></el-option>
          </el-select>
          </el-form-item>  
          <el-form-item label="作案人数" prop="caseuserCount" label-width="150px">
            <el-input v-model="dialogForm.caseuserCount"  placeholder="请填写作案人数"></el-input>
          </el-form-item>                    
          <el-form-item label="抓捕人数" prop="arrestCount" label-width="150px">
            <el-input v-model="dialogForm.arrestCount"  placeholder="请填写抓捕人数"></el-input>
          </el-form-item>
          <el-form-item label="案（事）件情况" prop="caseDesc" label-width="150px" >
            <el-input v-model="dialogForm.caseDesc"  placeholder="请填写案（事）件情况"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12"> 
           <el-form-item label="主犯（嫌疑人）证件号码" prop="suspectsCardNo" label-width="170px">
            <el-input v-model="dialogForm.suspectsCardNo"  :maxlength="30"  placeholder="请填写主犯（嫌疑人）证件号码"></el-input>
          </el-form-item>
           <el-form-item label="在逃人数" prop="escapedCount" label-width="150px">
            <el-input v-model="dialogForm.escapedCount"  placeholder="请填写在逃人数"></el-input>
          </el-form-item>
          <el-form-item label="是否破案" prop="isCaseSolve" label-width="150px">
            <el-radio-group v-model="dialogForm.isCaseSolve">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>                   
          <el-form-item label="案件侦破情况" prop="detectedDesc" label-width="150px">
            <el-input v-model="dialogForm.detectedDesc" :rows="2" placeholder="请填写案件侦破情况"></el-input>
          </el-form-item>
        </el-col>
      </el-row>              
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button type="warning" size="small" @click="resetForm">取消</el-button>
      <el-button type="success" size="small" @click="submitForm">保存</el-button>
    </div>
  </el-dialog>
</template>

<script>
import service from "./services/campus_security_service.js";

export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      schListOption: [],
      dic: {
        案事件性质: [],
        证件代码: [],
        行政区划: []
      },
      disCodeOption: [],
      dialogForm: {
        isCaseSolve: 0,
        districtCode: "",
        casePlace: "610000",
        happenPlace: "",
        suspectsIdcode: "111"
      },
      teamRules: {
        caseNumber: [
          {
            required: true,
            message: "请输入案件编号",
            trigger: "blur"
          },
          {
            min: 1,
            max: 20,
            message: "长度在 1 到 20 个字符",
            trigger: "blur"
          }
        ],
        caseName: [
          {
            required: true,
            message: "请输入案件名称",
            trigger: "blur"
          },
          {
            min: 2,
            max: 100,
            message: "长度在 2 到 100 个字符",
            trigger: "blur"
          }
        ],
        schoolId: [
          { required: true, message: "请选择学校", trigger: "change" }
        ],
        happenDate: [
          {
            required: true,
            message: "请选择发生日期",
            trigger: "change"
          }
        ],
        casePlace: [
          {
            required: true,
            message: "请选择发案地",
            trigger: "change"
          }
        ],
        happenPlace: [
          { message: "请输入发生地点", trigger: "blur" },
          {
            min: 2,
            max: 80,
            message: "长度在 2 到 80 个字符",
            trigger: "blur"
          }
        ],
        caseNature: [
          {
            required: true,
            message: "请选择案（事）件性质",
            trigger: "change"
          }
        ],
        caseDesc: [
          { message: "请输入案（事）件情况", trigger: "blur" },
          {
            min: 2,
            max: 4000,
            message: "长度在 2 到 4000 个字符",
            trigger: "blur"
          }
        ],
        suspectsIdcode: [
          {
            message: "请选择主犯（嫌疑人）证件号码",
            trigger: "change"
          }
        ],
        suppectsCardNo: [
          { message: "请输入主犯（嫌疑人）证件号码", trigger: "blur" }
        ],
        suppectsName: [
          { message: "请输入主犯（嫌疑人）姓名", trigger: "blur" },
          {
            min: 2,
            max: 5,
            message: "长度在 2 到 20 个字符",
            trigger: "blur"
          }
        ],
        isCaseSolve: [
          {
            required: true,
            message: "请选择是否破案",
            trigger: "change"
          }
        ],
        caseuserCount: [
          { message: "请输入作案人数", trigger: "blur" },
          {
            min: 1,
            max: 6,
            message: "长度在 1 到 24 个字符",
            trigger: "blur"
          }
        ],
        escapedCount: [
          { message: "请输入在逃人数", trigger: "blur" },
          {
            min: 1,
            max: 6,
            message: "长度在 1 到 24 个字符",
            trigger: "blur"
          }
        ],
        arrestCount: [
          { message: "请输入抓捕人数", trigger: "blur" },
          {
            min: 1,
            max: 6,
            message: "长度在 1 到 24 个字符",
            trigger: "blur"
          }
        ],
        detectedDesc: [
          { message: "请输入案件侦破情况", trigger: "blur" },
          {
            min: 2,
            max: 4000,
            message: "长度在 2 到 4000 个字符",
            trigger: "blur"
          }
        ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },
  watch: {
    "dialogForm.districtCode": {
      handler(code) {
        if (code) {
          service.requestCodeByAddress(code, data => {
            if (data.belongname) {
              this.dialogForm.happenPlace = data.belongname + data.name;
            } else {
              this.dialogForm.happenPlace = "铜川市";
            }
          });
        }
      }
    }
  },
  methods: {
    handleChangeCode(id) {
      if (id) {
        let code = this.schListOption.find(item => item.id == id).districtCode;
        // 根据选中项重新获取区域编码
        this.dialogForm.districtCode = code;
      }
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          param.happenDate = util.time.formatDate(
            param.happenDate,
            "YYYY-MM-DD"
          );

          if (this.isAdd == true) {
            // 添加

            service.requestSavePreferstCase(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "信息添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdatePreferstCase(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "信息修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;

      this.dialogForm = {
        isCaseSolve: 0,
        districtCode: "",
        casePlace: "610000",
        happenPlace: "",
        suspectsIdcode: "111"
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-dialog .el-select {
  width: 100%;
}
.add-dialog .el-cascader {
  width: 100%;
}
</style>